import React, { PropTypes } from 'react';
// import FacebookLogin from 'react-facebook-login';
import styled from 'styled-components';
import { Form1, Form2, Title4Top, Separator, Input, Center, Button3, Total, SmallText, Link, Note, Row,
  Choice, ErrorText } from './styledComponents';
import CreditCard from './CreditCard';
import checkZipcodeError from '../services/zipcode';
import mangopayImg from '../assets/mangopay.png';

const Img = styled.img`
  object-fit: contain;
  width: 100%;
`;

class InfosBlock extends React.Component {
  constructor() {
    super();
    this.state = {};
    this.inputChange = this.inputChange.bind(this);
    this.submitRegister = this.submitRegister.bind(this);
    this.submitLoggedInOrder = this.submitLoggedInOrder.bind(this);
    this.submit = this.submit.bind(this);
  }

  componentWillReceiveProps(nextProps) {
    if (nextProps.isLoggued !== this.props.isLoggued) {
      const { firstname, lastname, password, email, phone, address } = nextProps.customer || {};
      const { street, zipcode, locality, comment } = address || {};
      const isLoggued = nextProps.isLoggued;
      this.setState({ firstname, lastname, password, email, phone, street, zipcode, locality, comment, isLoggued });
    }
  }

  inputChange(field, value) {
    if (field === 'zipcode') {
      this.setState({ zipcodeError: checkZipcodeError(value) });
    } else if (field === 'passwordCheck') {
      this.setState({ passwordCheckError: value !== this.state.password });
    }
    this.setState({ [field]: value });
  }

  submitRegister() {
    const { firstname, lastname, password, email, phone, street, zipcode, locality, comment, expiryDate, cvc,
      cardNumber, cardId } = this.state;
    const address = { street, zipcode, locality, comment };
    const customer = { firstname, lastname, password, email, phone, address };
    const card = { expiryDate, cvc, cardNumber, cardId };
    this.props.submitRegister(customer, card);
  }

  submitLoggedInOrder() {
    const { street, zipcode, locality, comment, expiryDate, cvc, cardNumber, cardId } = this.state;
    const customer = { address: { street, zipcode, locality, comment } };
    const card = { expiryDate, cvc, cardNumber, cardId };
    this.props.submitLoggedInOrder(customer, card);
  }

  isValid() {
    const fields = ['firstname', 'lastname', 'email', 'phone', 'street', 'zipcode', 'locality'];
    const passwordSelected = this.state.password || !this.state.newAccount;
    const cardSelected = (this.state.expiryDate && this.state.cvc && this.state.cardNumber) || this.state.cardId;
    const emptyFields = fields.filter(field => !this.state[field]);
    return emptyFields.length === 0 && !this.state.passwordCheckError && !this.state.zipcodeError && cardSelected
      && passwordSelected;
  }

  submit() {
    if (!this.isValid()) {
      return this.setState({ formError: true });
    }
    return this.state.newAccount ? this.submitRegister() : this.submitLoggedInOrder();
  }

  renderForm() {
    const { rdv } = this.props;
    const rdvDate = rdv.format('D MMMM');
    const rdvBegin = rdv.format('H[h]mm');
    const rdvEnd = rdv.clone().add(30, 'minute').format('H[h]mm');
    return (
      <div>
        <Form1>
          <Title4Top>Mes coordonnes</Title4Top>
          <Row>
            <Input
              half type="text" placeholder="Votre nom" value={this.state.lastname}
              onChange={e => this.inputChange('lastname', e.target.value)}
            />
            <Input
              half type="text" placeholder="Votre prnom" value={this.state.firstname}
              onChange={e => this.inputChange('firstname', e.target.value)}
            />
          </Row>
          <Row>
            <Input
              half type="email" placeholder="Adresse email" value={this.state.email}
              onChange={e => this.inputChange('email', e.target.value)}
            />
            <Input
              half type="tel" placeholder="Tlphone" value={this.state.phone}
              onChange={e => this.inputChange('phone', e.target.value)}
            />
          </Row>
          {this.state.newAccount &&
            <Input
              type="password" placeholder="Votre mot de passe"
              onChange={e => this.inputChange('password', e.target.value)}
            />}
          {this.state.newAccount &&
            <Input
              type="password" placeholder="Confirmer votre mot de passe"
              onChange={e => this.inputChange('passwordCheck', e.target.value)}
            />}
          {this.state.passwordCheckError && <ErrorText>Les 2 mots de passe ne correspondent pas</ErrorText>}

          <Title4Top>Mon adresse</Title4Top>
          <Input
            type="text" placeholder="Voie" value={this.state.street}
            onChange={e => this.inputChange('street', e.target.value)}
          />
          <Row>
            <Input
              half type="text" placeholder="Code postal" value={this.state.zipcode}
              onChange={e => this.inputChange('zipcode', e.target.value)}
            />
            <Input
              half type="text" placeholder="Ville" value={this.state.locality}
              onChange={e => this.inputChange('locality', e.target.value)}
            />
          </Row>
          {this.state.zipcodeError}
          <Input
            type="text" placeholder="Complment (digicode, tage, etc)" value={this.state.comment}
            onChange={e => this.inputChange('comment', e.target.value)}
          />
        </Form1>

        {/* <Link2 href="#">Ajouter une nouvelle adresse</Link2> */}
        <Title4Top>Mon moyen de paiement</Title4Top>
        {this.state.newAccount ?
          <CreditCard onChange={this.inputChange} />
          : <div>
            {this.state.showCreditCard ?
              <CreditCard onChange={this.inputChange} />
              : <div>
                {this.props.customer.cards && this.props.customer.cards.map(card =>
                  <Choice
                    isSelected={this.state.cardId === card.id} key={card.id}
                    onClick={() => this.inputChange('cardId', card.id)}
                  >
                    {card.alias} - {card.expirationDate.slice(0, 2)} / {card.expirationDate.slice(2, 4)}
                  </Choice>,
                )}
                <Choice onClick={() => this.setState({ showCreditCard: true })}>
                  Ajouter une nouvelle carte bancaire
                </Choice>
              </div>
            }
          </div>
        }

        {this.state.showPromoCode ?
          <div>
            <Title4Top>Carte cadeau/code promo</Title4Top>
            <Center>
              <Input
                type="text" placeholder="code" value={this.state.promoCode}
                onChange={e => this.inputChange('promoCode', e.target.value)}
              />
              <Button3 small noMargin>Appliquer</Button3>
            </Center>
          </div> : <Center onClick={() => this.setState({ showPromoCode: true })}>
            <Link>+ Ajouter une carte cadeau ou un code promo</Link></Center>
        }

        <Title4Top>Rcapitulatif</Title4Top>
        <Total>
          Rdv le {rdvDate} entre {rdvBegin} et {rdvEnd}
        </Total>
        <Total>
          Montant total : {this.props.total};
        </Total>
        {this.state.formError && <ErrorText>Merci de remplir tous les champs</ErrorText>}
        <Center>
          <Button3
            isNotValid={!this.isValid()}
            onClick={this.submit}
          >Valider mon RDV</Button3>
        </Center>
        <SmallText>
          {'En validant, vous acceptez les '}
          <Link href="#">
            conditions gnrales
          </Link>
          {' de vente de tilli'}
        </SmallText>
        <Note>
          {`Le mimimun de commande est de 20. Si votre commande est infrieure  ce montant, des frais de livraison
            seront appliqus.`}
        </Note>
        <Img src={mangopayImg} alt="MangoPay notre partenaire de paiement" />
      </div>
    );
  }

  renderLogin() {
    return (
      <Form2>
        {/* <Separator />
        <FacebookLogin
          appId="258420877937846"
          autoLoad
          fields="name,email,picture"
          onClick={(...args) => console.log('ComponentClick', args)}
          callback={(...args) => console.log(args)}
          version="2.8"
          language="fr"
          textButton="Se connecter avec Facebook"
        /> */}
        <Separator />
        <Title4Top>Dj client ?</Title4Top>
        <Input
          type="email" placeholder="Votre adresse email"
          onChange={e => this.inputChange('email', e.target.value)}
        />
        <Input
          type="password" placeholder="Votre mot de passe"
          onChange={e => this.inputChange('password', e.target.value)}
        />
        {this.props.isLoginLoading ?
          <p>chargement</p> :
          <Button3 type="button" onClick={() => this.props.login(this.state.email, this.state.password)}>
            Se connecter
          </Button3>
        }
        {this.props.errorLogin && <ErrorText>Email/mot de passe incorrect(s)</ErrorText>}
        <Separator />
        <Title4Top>Pas encore de compte ?</Title4Top>
        <Button3 type="button" onClick={() => this.setState({ isLoggued: true, newAccount: true })}>
          {"S'inscrire"}
        </Button3>
        <Separator />
      </Form2>
    );
  }

  render() {
    if (this.state.isLoggued) {
      return this.renderForm();
    }
    return this.renderLogin();
  }
}

InfosBlock.propTypes = {
  total: PropTypes.number.isRequired,
  submitRegister: PropTypes.func.isRequired,
  submitLoggedInOrder: PropTypes.func.isRequired,
  login: PropTypes.func.isRequired,
  customer: PropTypes.shape({
    cards: PropTypes.arrayOf(PropTypes.shape({})),
  }).isRequired,
  isLoggued: PropTypes.bool.isRequired,
  errorLogin: PropTypes.bool.isRequired,
  isLoginLoading: PropTypes.bool.isRequired,
  rdv: PropTypes.shape({}).isRequired,
};

export default InfosBlock;



